import { mapState } from "vuex";
const Cookie = process.client ? require("js-cookie") : "";
export default {
  name: "LayoutIndex",
  computed: {
    ...mapState(["user"]),
  },
  methods: {
    logout() {
      // 删除客户端缓存数据
      this.$store.commit("setUser", null);
      // 删除服务端数据持久化
      Cookie.set("user", null);
      this.$router.push("/");
    },
  },
  components: {},
};
